import React from 'react'
import Img from "../defaults/image"



const Card = (props) => {
    const { image, title, text, date, width, heading, headText } = props.data;
    return (
        <>

            <div className={heading ? "card-main flexed" : "card-main"}>
                {
                    heading && <div className="wText" style={{ width: "37vw", marginRight: "10%" }}>

                        <h1>{heading}</h1>
                        <span>{headText}</span>
                    </div>
                }
                <div>
                    <div className="slider-images" style={{ maxWidth: `${width}px`, width: "40vw" }}>
                        <Img image={image} />
                    </div>
                    <div className="card-text">
                        <h3>{title}</h3>
                        <span className="text-span">{text}</span>
                        <span >{date}</span>
                    </div>
                </div>

            </div>
        </>
    );
}

export default Card;
